<template>
  <div>
    <div class="input-contain">
      <input
        class="search"
        type="text"
        placeholder="Search by name or symbol"
        @keyup="searchCoin()"
        v-model="search"
      />
    </div>
    <TableCoin :coins="this.send"></TableCoin>
  </div>
</template>

<script>
import TableCoin from "./components/TableCoin.vue";
export default {
  name: "App",
  data() {
    return {
      coins: [],
      search: "",
      send: []
    };
  },
  async mounted() {
    const res = await fetch(
      "https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1"
    );
    const coins = await res.json();
    this.coins = coins;
    this.send = coins;
  },
  components: {
    TableCoin,
  },
  methods: {
    searchCoin() {
      this.send = this.coins.filter(
        (coin) =>
          coin.name.toLowerCase().includes(this.search.toLowerCase()) ||
          coin.symbol.toLowerCase().includes(this.search.toLowerCase())
      );
    },
  },
};
</script>

<style lang="scss">
body {
  background-color: #252850;
}
.input-contain {
  display: flex;
  justify-content: center;
  .search {
    margin: 5em;
    width: 30em;
    height: 3em;
    background-color: #080b52;
    border-radius: 1em;
    border: none;
    padding: 1em;
    color: white;
    font-size: 1em;
  }
  .search::placeholder {
    color: white;
    opacity: 0.3;
  }
}
</style>
